export default {
	data() {
		return {
			imgUrl: this.$imgUrl,
			current: 0,
			info: [],
			caseExclusive: []
		};
	},
	onLoad:function(option){
		this.gettingData()
		this._getSharingBannerList()
	},
	methods: {
		change (e) {
			this.current = e.detail.current;
		},
		// 获取banner图
		_getSharingBannerList () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('System/get_sharing_banner_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.info = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		gettingData () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('System/get_sharing_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.caseExclusive = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		spikCaseDetails (item) {
			var items = JSON.stringify(item)
			uni.navigateTo({
				url: '../caseDetails/caseDetails?items=' + items
			})
		}
	}
}
